.header__top {
  padding-top: 20px;
  padding-bottom: 6px;
}
.header__top-content {
  display: flex;
  justify-content: space-between;
}
.header__top-left {
  display: flex;
  align-items: center;
}
.header__top-right {
  display: flex;
  align-items: center;
}
.header__top-logo {
  border-radius: 6px;
  border: 1px solid var(--blue);
  width: 28px;
  height: 28px;
  padding: 1px;
  margin-right: 8px;
}
.header__top-img {
  border-radius: 3.34px;
}